body, input { color: #aaa; }

h5 {
  margin-top: 0;
}

textarea,
input.text,
input[type="text"],
input[type="button"],
input[type="submit"],
.input-checkbox {
-webkit-appearance: none;
border-radius: 0;
}
input[type=text] { font-size: 1em; }

ul {
  margin: 0;
  padding: 0;
}

.App { margin: 1em; }

.contrast { color: #eeaa32; }

.right { float: right; }



