.add-form {
	border: 2px solid #eee;
	float: left;
	margin: 0 1em 1em 0;
	padding: 1em;
}

.add-form { text-transform: uppercase; }

ul.list-box {
	font-size: 1em;
	list-style-type: none;
	margin-bottom: .5em;
}

.list-box li {
	background-color: #fff;
	border-bottom: 1px solid #eee;
	padding: .25em;
	margin-bottom: .2em;
}

.list-box li input[value=x] { visibility: hidden; font-size: .75em; }
.list-box li:hover input, .list-box li:active input { cursor: pointer; visibility: visible; }

.list-box input.button {
	border: 2px solid #f9f5ee;
	margin: .5em auto;
	text-align: center;
}



