body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}

.small {
	font-size: .75em;
	font-weight: normal;
}

input { border-bottom: none;
		border-left: none;
		border-top: none;
		border-right: none;
}
input[type=text] { border-bottom: 3px solid #fbebcf; }
input[type=submit] { cursor: pointer; color: #ddd; }
input[type=submit]:disabled { color: #ddd; }
input[type=submit]:not(disabled) { color: #aaa; }

.add-form input[type=text] {
	border-bottom: 3px solid #eee;
	font-size: 1em;
}



